import React, { Component } from 'react'
import Slider from 'react-slick'
import thumb from './assets/thumb.png'
import logo from './assets/logo.png'
import customCenter from './assets/customCenter.png'
import rightControl from './assets/rightControl.png'
import cmpstructure from './assets/cmpstructure.png'
import redux from './assets/redux.png'
import work from './assets/work.png'
import board from './assets/board.png'
import pcLogo from './assets/pcLogo.png'
import tools from './assets/tools.png'
import social from './assets/social.png'
import slogan1Light from './assets/slogan1Light.png'
import slogan2Light from './assets/slogan2Light.png'
import slogan3Light from './assets/slogan3Light.png'
import learn1 from './assets/learn1.png'
import learn2 from './assets/learn2.png'
import learn3 from './assets/learn3.png'
import fail from './assets/fail.png'
import download from './assets/download.png'
import account from './assets/account.png'
import prd from './assets/prd.png'
import swagger from './assets/swagger.png'
import tdd from './assets/tdd.png'
import dontknow from './assets/dontknow.jpeg'
import speed from './assets/speed.jpeg'
import focus from './assets/focus.jpg'
import './App.css'

const profit = [
  {
    head: '',
    title: '',
    detail: [
      {
        caption: '',
        img: social
      },
      {
        caption: '',
        img: board
      },
      {
        caption: '',
        img: pcLogo
      },
      {
        caption: '',
        img: tools
      }
    ]
  },
  {
    head: '',
    title: '',
    detail: [
      {
        caption: '',
        img: learn1
      },
      {
        caption: '',
        img: learn2
      },
      {
        caption: '',
        img: learn3
      }
    ]
  },
  {
    head: '',
    title: 'bugs...',
    detail: [
      {
        caption: 'cmp',
        img: fail
      },
      {
        caption: '',
        img: download
      },
      {
        caption: '',
        img: account
      }
    ]
  },
  {
    head: '',
    title: '...',
    detail: [
      {
        caption: '',
        img: swagger
      },
      {
        caption: '',
        img: tdd
      },
      {
        caption: '',
        img: prd
      }
    ]
  },
  {
    head: '',
    title: '...',
    detail: [
      {
        caption: '',
        img: speed
      },
      {
        caption: '',
        img: focus
      },
      {
        caption: '',
        img: dontknow
      }
    ]
  },
  {
    head: '',
    title: '',
    detail: [
      {
        caption: '',
        img: slogan1Light
      },
      {
        caption: '',
        img: slogan2Light
      },
      {
        caption: '',
        img: slogan3Light
      }
    ]
  }
]

class App extends Component {
  render() {
    var slickSettings = {
      autoplay: false,
      accessibility: false,
      dots: true,
      arrows: true,
      swipeToSlide: true,
      infinite: true,
      speed: 100,
      autoplaySpeed: 1000,
      slidesToShow: 1,
      slidesToScroll: 1,
      fade: false,
      pauseOnHover: false
    }
    const profits = profit.map((p, i) => {
      let imgs = p.detail.map((d, ind) => (
        <div key={`detail${ind}`} className="profitDetail">
          <img src={d.img} alt={d.caption} />
          <p>{d.caption}</p>
        </div>
      ))
      return (
        <div className="page">
          <h3>{p.head}</h3>
          <div className="profitPage">
            <p className="title">{p.title}</p>
            <div className="profitItems">{imgs}</div>
          </div>
        </div>
      )
    })
    return (
      <div className="App">
        <header className="App-header">
          <img src={thumb} className="App-logo" alt="logo" />
          <h1>2017</h1>
        </header>
        <section id="sliderZone">
          <Slider {...slickSettings}>
            <div className="page">
              <div className="cover">
                <h1></h1>
                <img src={work} alt="cover" />
              </div>
            </div>
            <div className="page">
              <h3></h3>
              <div className="workPage">
                <p></p>
                <img src={rightControl} alt="rightControl" />
              </div>
            </div>
            <div className="page">
              <h3></h3>
              <div className="workPage">
                <p></p>
                <img src={customCenter} alt="customcenter" />
              </div>
            </div>
            <div className="page">
              <h3></h3>
              <div className="workPage">
                <p>1 CMP, Redux</p>
                <img src={cmpstructure} alt="customcenter" />
              </div>
            </div>
            <div className="page">
              <h3></h3>
              <div className="workPage">
                <p>2 : </p>
                <img src={cmpstructure} alt="customcenter" />
              </div>
            </div>
            <div className="page">
              <h3></h3>
              <div className="workPage">
                <p>3 </p>
                <img src={cmpstructure} alt="customcenter" />
              </div>
            </div>

            {profits}
          </Slider>
        </section>
        <footer>
          <img src={logo} alt="logo" />
          <h1 className="App-title"></h1>
        </footer>
      </div>
    )
  }
}

export default App



